body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.menu-item {
  font-size: 36pt;
  color: white;
  background-color: darkslategray;
  margin-top: .5em;
  padding: .25em;
  border-radius: .5em;
  width: 7em;
  text-align: center;
  cursor: pointer; }

.menu-page {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
}

.monster-viewer-header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    width: 98%
}

.mvh-row {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
}

.mvh-left-margin {
    margin-left: 10px;
}

.mvh-filter {
    width: 12em;
}

.modal {
    position: fixed;
    top: 0;
    left: 0;
    width:100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.6);
  }
  
  .modal-main {
    position:fixed;
    background: rgb(39, 39, 39);
    min-width: 50%;
    height: auto;
    top:50%;
    left:50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
  }
  
  .display-block {
    display: block;
  }
  
  .display-none {
    display: none;
  }
.monster-form {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    background-color: antiquewhite;
    overflow-y: scroll;
    padding: .5em;
    max-height: 90vh;
    border-radius: .5em;
    min-width: 18em;
}

.monster-form-section {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    padding-top: 10px;
    padding-bottom: 10px;
    width: 100%;
}

.number-size {
    width: 3em;
}

.mm-hit-die {
    width: 5em;
}

.textarea-size {
    min-width: 20em;
    height: 5em;
}

.column-container {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
}

.row-container {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: flex-start;
    flex-wrap: wrap;
    width: 100%;
}

.physical-stat-section {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: flex-start;
    flex-wrap: wrap;
    margin-top: 10px;
}

.mental-stat-section {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: flex-start;
    flex-wrap: wrap;
    margin-top: 10px;
}

.row-container-space-between {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}

.center-container {
    justify-content: center;
    align-items: center;
}

.monster-form-row-section {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: flex-start;
    flex-wrap: wrap;
}

.left-margin {
    margin-left: 10px;
}

.right-margin {
    margin-right: .5em;
}

.mm-align-left {
    text-align: left;
}

.mm-margin-element {
    margin: .25em;
}

.side-margin {
    margin-left: 10px;
    margin-right: 10px;
}

.top-margin {
    margin-top: 10px;
}

.border-bottom {
    border-bottom: 2px solid darkslategray;
}

.red-text {
    color: darkred;
}

.bold-text {
    font-weight: bold;
}

.lm-modal {
    position: fixed;
    top: 0;
    left: 0;
    width:100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.6);
  }
  
  .lm-modal-main {
    position:fixed;
    min-width: 50%;
    height: auto;
    top:50%;
    left:50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }
  
  .display-block {
    display: block;
  }
  
  .display-none {
    display: none;
  }
.soem-container {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
