    overflow-y: scroll;
    max-height: 90vh;
    max-width: 100vw;
    border-radius: .5em;
}

.soem-element {
    margin: .25em;
}

.soem-button-row {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-content: center;
    width: 100%;
}
.mv-view {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center; }

.mv-monster {
  background-color: darkslategray;
  color: white;
  width: 20em;
  margin-top: .25em;
  margin-bottom: .25em;
  padding: .25em;
  border-radius: .5em;
  cursor: pointer; }

.player-viewer-header {
