  justify-content: flex-start;
  align-items: center; }

.el-encounter {
  display: flex;
  flex-direction: column;
  background-color: darkslategray;
  color: white;
  width: 15em;
  margin-top: .25em;
  margin-bottom: .25em;
  padding: .25em;
  border-radius: .5em; }

.el-margin-top {
  margin-top: 1em; }

.el-row-force-space {
